.checkbox-label,
.vacan-form-checkbox-label {
  width: 100%;
  color: #06631b;
  font-family: Montserrat;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 120%; /* 19.2px */
  position: relative;
  padding-left: 30px;
  cursor: pointer;
  user-select: none;
  @media screen and (max-width: 767px) {
    font-size: 13px;
    padding-left: 20px;
  }
}

.vacan-checkbox + label::before,
.vacan-form-checkbox + label::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  transform-origin: top left;
  // transform: translateY(-50%);
  width: 20px;
  height: 20px;
  border: 2px solid #04a427;
  border-radius: 4px;
  background-color: transparent;
  box-sizing: border-box;
  min-height: 20px;
  // position: relative;
  // border: 1px solid red;
  @media screen and (max-width: 767px) {
    width: 15px;
    min-height: 15px;
    height: 15px;
    border: 1px solid #04a427;
  }
}
.vacan-form-checkbox + .policy::before {
  top: 50%;
  transform: translateY(-50%);
}

.vacan-checkbox:checked + label::before,
.vacan-form-checkbox:checked + label::before {
  background-color: #04a427;
  border-color: #04a427;
  // display: flex;
  // align-items: center;
  // justify-content: center;
  // border: 1px solid red;
}
.vacan-checkbox:checked + label::after,
.vacan-form-checkbox:checked + label::after {
  content: '';
  position: absolute;
  left: 6px;
  top: 2px;
  transform: translateY(-50%);
  width: 8px;
  height: 12px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
  @media screen and (max-width: 767px) {
    left: 5px;
    top: 1;
    width: 6px;
    height: 10px;
  }
}
.vacan-form-checkbox:checked + .policy::after {
  top: 30%;
}

