  top: 75%;
  left: 70%;
  transform: translate(-75%, -70%) rotate(-30deg);
}
.bigBtnGreen:hover {
  box-shadow: 0 0 20px rgba(53, 114, 3, 0.9);
  // @media screen and (max-width: 767px) {
  //   animation: rotateBtnSmall 2s infinite linear;
  // }
  animation: rotateBtn 2s infinite linear;
}

@keyframes rotateBtn {
  0%,
  100% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(-30deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(30deg);
  }
}
@keyframes rotateBtnSmall {
  0%,
  100% {
    transform: translateX(50%) rotate(0deg);
  }
  25% {
    transform: translateX(50%) rotate(-30deg);
  }
  50% {
    transform: translateX(50%) rotate(0deg);
  }
  75% {
    transform: translateX(50%) rotate(30deg);
  }
}

